@use 'sass:color';
@use '../../base/' as *;
@use '../../utils/' as *;

.btn-score{
  @extend %circle, %roundedContainer;
  color: $light-grey;
  transition: 
    color 100ms ease-out,
    background-color 100ms ease-out;
  &:hover{
    background-color: $light-grey;
    color: $white;
  }
  label{
    cursor: pointer;
  }
}