@use '../base/' as *;

.inactive{
  display: none !important;
}
.btn-rating--selected{
  color: $white !important;
  background-color: $prymary !important;
}

.highlightedText{
  @extend %roundedContainer;
  padding: .5rem 1rem;
  color: $prymary;
}
%circle{
  width: 2.5rem;
  height: 2.5rem;
}

%roundedContainer{
  border-radius: 50vmin;
  background-color: lighten($dark-blue, 5%);
}

%card{
  width: 300px;
  height: auto;
  margin: auto;
  background-color: $dark-blue;
  color: $medium-grey;
  border-radius: 1rem;
  padding: 2rem;
  box-shadow: 0 10px 10px 5px $black;
  display: grid;
  align-items: center;
  row-gap: 1rem;
  @media screen and (min-width: 500px){
    width: clamp(300px, 50vw, 370px);
  }
}