@use '../../base/' as *;

.btn-submit{
  width: 100%;
  border-radius: 50vmax;
  background-color: $prymary;
  color: $white;
  text-transform: uppercase;
  letter-spacing: 2px;
  padding: 14px 0;
  transition: 
    color 200ms ease-out,
    background-color 200ms ease-out;
  &:hover{
    color: $prymary;
    background-color: $white;
  }
}