@use 'variables' as *;
@use 'typography' as *;

*, *::after, *::before{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html{
  font-size: 100%;
}

body{
  font-family: $ff;
  font-size: $fs-body;
}

button{
  outline: unset;
}

a{
  color: $secondary; 
}

h1, h2{
  color: $white;
}

input[type="radio"]{
  display: none;
}

strong{
  font-weight: normal;
}