@use '../../base/' as *;
@use '../../utils/' as *;

.card-form, .card-thankYou{
  @extend %card;
}

.card-form{
  figure{
    @extend %circle, %roundedContainer;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .contenedor{
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
}