@use '../base/' as *;

footer{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 1rem;
  font-size: $fs-footer;
  color: $light-grey;
  letter-spacing: 1px;
  @media screen and (min-width: 500px) {
    flex-direction: row;
    column-gap: .4rem;
  }
  a{
    color: $secondary;
    text-decoration: none;
  }
  a:hover{
    color: $white;
  }
}